<template>
	<footer class="footer" style="">
		<span class="todo-count"><strong>{{leftSum }}</strong> items left</span> 
		<ul class="filters">
			<li><a href="#" :class="{selected:optionCur=='all'}"
				@click="$store.dispatch('changeOption','all')">all</a></li>
			<li><a href="#" :class="{ selected: optionCur == 'active' }"
				@click="$store.dispatch('changeOption','active')">active</a></li>
			<li><a href="#" :class="{ selected: optionCur == 'completed' }"
				@click="$store.dispatch('changeOption','completed')">completed</a></li>
		</ul> 
		<button class="clear-completed"
		v-show="!disAllCheck"
		@click="clearDone">clear completed</button>
	</footer>
</template>

<script>
import { mapState, mapGetters ,mapMutations, mapActions} from 'vuex';
export default {
  	name: 'TodoFooter',
	computed: {
		...mapState(['optionCur']),
		...mapGetters(['leftSum','disAllCheck'])
	},
	methods: {
		...mapMutations(['clearDone', 'changeOption']),
		...mapActions(['changeOption'])
	}
}
</script>

