<template>
  <section class="todoapp">
    <TodoHeader></TodoHeader>
    <TodoMain></TodoMain>
    <TodoFooter></TodoFooter>
  </section>
</template>

<script>
import TodoHeader from './components/TodoHeader.vue'
import TodoMain from './components/TodoMain.vue'
import TodoFooter from './components/TodoFooter.vue'
// 使用vuex中的mapStore
import {mapState,mapGetters,mapMutations,mapActions} from 'vuex'
// export default {}形式导入的文件，导入的是个obj，以此方式接收并“obj.方法()”使用
// import local from '@/utils/localStorage.js'
// export{}解构形式导出的js文件，函数定义、变量名直接被解构导入，可直接“方法()”调用
// import { saveData, getData } from '@/utils/localStorage.js'

export default {
  name: 'App',
  components: {
    TodoHeader,
    TodoMain,
    TodoFooter,
  },
  data() {
    return {
      // 这里无需定义数据属性，只需在store中定义即可
    }
  },
  created() {
    //从数据库中获取数据，放入state
    const todoData = this.$getData();
    if (todoData) {
      // 若本地有数据，自动加载入store
      this.$store.replaceState(todoData);
    }
    // 关闭页面之前，将state中数据自动保存至localStorage
    window.addEventListener('beforeunload', () => {
      if (!this.dataList.length) {
        // 没数据时，自动保存 模板数据
        this.$saveData({
          isLoading: false,
          optionCur: 'all',
          dataList: [
            { text: '吃点什么呢？？？？', status: false, id: 0 },
            { text: '人嘛！总得有点追求.....', status: false, id: 2 },
            { text: '时不我待', status: true, id: 3 },
          ]
        });
      } else {
         this.$saveData(todoData);
      }
    });
  },
  computed: {
    ...mapState(['dataList'])
  }
}
</script>

