<template>
  		<header class="header">
  			<h1>todos</h1> 
  			<input type="text" placeholder="what need to be done?" class="new-todo"
        @keyup.enter="addItemF">

        <input type="checkbox" class="toggle-all"
        :disabled="disAllCheck"
        :checked="allSumStaus"
        @change="changeSumStaus(!allSumStaus)"/> 
  		</header>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';

export default {
  name: 'TodoHeader',
  computed: {
    ...mapGetters(['disAllCheck','allSumStaus']),
  },
  methods: {
    ...mapMutations(['addItem', 'changeSumStaus']),
    addItemF(e) {
      let val = e.target.value.trim();
      if (val != '') {
        this.addItem(val);
        e.target.value = '';
      }
    }
  }
}
</script>

